
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;700&family=Orbitron:wght@400;700&display=swap');

body {
    --bg-color: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
    --text-color: #e0e0e0;
    --primary-color: #00ffde;
    --secondary-color: #1a1a1a;
    --accent-color: #ff4d4d;
    --border-color: #00ffde;
    --active-bg-color: rgba(0, 255, 222, 0.2);
    --menu-bg-color: rgba(26, 26, 26, 0.9);

    background: var(--bg-color);
    color: var(--text-color);
    font-family: 'Noto Sans KR', 'Orbitron', sans-serif;
    overflow: hidden;
    height: 100vh;
}

@keyframes animStar {
    from {
        transform: translateY(0);
    }
    to {
        transform: translateY(-2000px);
    }
}

.stars-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    overflow: hidden;
}

.stars, .stars2, .stars3 {
    position: absolute;
    top: 0;
    left: 0;
    background: transparent;
    animation-name: animStar;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

.stars {
    width: 1px;
    height: 1px;
    box-shadow: 538px 1573px #FFF, 1859px 1656px #FFF, 158px 1038px #FFF, 1503px 143px #FFF, 1432px 1238px #FFF, 1030px 1305px #FFF, 1322px 145px #FFF, 1842px 1139px #FFF, 110px 1622px #FFF, 130px 1215px #FFF, 1413px 1507px #FFF, 1033px 133px #FFF, 504px 134px #FFF, 129px 1232px #FFF, 1448px 133px #FFF, 1389px 1383px #FFF, 1559px 103px #FFF, 1488px 491px #FFF, 1095px 138px #FFF, 1352px 1229px #FFF, 1334px 111px #FFF, 1899px 129px #FFF, 1133px 103px #FFF, 1796px 103px #FFF, 1191px 127px #FFF, 1435px 153px #FFF, 689px 105px #FFF, 125px 155px #FFF, 103px 125px #FFF, 153px 105px #FFF;
    animation-duration: 50s;
    animation-delay: 0s;
}

.stars2 {
    width: 2px;
    height: 2px;
    box-shadow: 1804px 1823px #FFF, 132px 1509px #FFF, 1808px 1134px #FFF, 153px 1023px #FFF, 123px 123px #FFF, 1234px 532px #FFF, 643px 123px #FFF, 843px 543px #FFF, 965px 243px #FFF, 754px 243px #FFF, 865px 231px #FFF, 965px 243px #FFF, 1234px 654px #FFF, 243px 654px #FFF, 564px 234px #FFF, 756px 243px #FFF, 865px 321px #FFF, 965px 234px #FFF, 1324px 654px #FFF, 243px 654px #FFF, 564px 324px #FFF, 756px 243px #FFF, 865px 321px #FFF, 965px 234px #FFF;
    animation-duration: 50s;
    animation-delay: -5s;
}

.stars3 {
    width: 3px;
    height: 3px;
    box-shadow: 1052px 1378px #FFF, 1933px 1569px #FFF, 1598px 1093px #FFF, 153px 123px #FFF, 1234px 532px #FFF, 643px 123px #FFF, 843px 543px #FFF, 965px 243px #FFF, 754px 243px #FFF, 865px 231px #FFF, 965px 243px #FFF, 1234px 654px #FFF, 243px 654px #FFF, 564px 234px #FFF, 756px 243px #FFF, 865px 321px #FFF, 965px 234px #FFF;
    animation-duration: 50s;
    animation-delay: -10s;
}

.title-only-container {
    display: flex;
    justify-content: center;
    align-items: flex-start; /* Changed to flex-start */
    height: 100vh;
    text-align: center;
    padding-top: 5rem; /* Added padding */
}

.main-container {
    display: flex;
    height: 100vh;
    padding: 2rem;
    box-sizing: border-box;
    align-items: center; /* Default alignment */
    justify-content: center; /* Default alignment */
}

.main-container.layout-split {
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
}

.main-container .clock-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1 1 50%; /* Flex properties */
    padding-right: 2rem;
}

.main-container.layout-split .schedule-board {
    flex: 1 1 30%; /* Flex properties */
    height: 100%;
    overflow-y: auto;
    padding: 2rem 2rem 8rem; /* Added bottom padding */
    border-left: 2px solid var(--border-color); /* Added separator */
    box-sizing: border-box; /* Ensure padding is included in height */
}

.bottom-logo-container {
    position: fixed;
    bottom: 0;
    left: 0;
    margin: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--primary-color);
    font-size: 1rem;
}

.logo-image {
    width: 8rem;
}

.main-title {
    font-size: 10rem;
    color: var(--primary-color);
    text-shadow: 0 0 15px var(--primary-color);
}

.top-nav {
    position: fixed;
    top: 2rem;
    left: 2rem;
    z-index: 10;
}

.menu-icon {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
}

.menu-icon-bar {
    width: 35px;
    height: 5px;
    background-color: var(--primary-color);
    margin: 6px 0;
    transition: 0.4s;
}

.menu-items {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50px;
    left: 0;
    background-color: var(--menu-bg-color);
    border: 1px solid var(--border-color);
    border-radius: 5px;
    padding: 1rem;
}

.nav-btn {
    background: none;
    border: none;
    color: var(--primary-color);
    font-family: 'Noto Sans KR', 'Orbitron', sans-serif;
    font-size: 1.2rem;
    padding: 0.5rem 1rem;
    margin: 0.5rem 0;
    cursor: pointer;
    transition: all 0.3s ease;
    text-align: left;
}

.nav-btn.active, .nav-btn:hover {
    color: var(--secondary-color);
    background-color: var(--primary-color);
    text-shadow: none;
}

.nav-btn.active, .nav-btn:hover {
    background-color: var(--primary-color);
    color: var(--secondary-color);
    box-shadow: 0 0 10px var(--primary-color);
}

.clock-only-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    text-align: center;
}

.clock {
    font-size: 8rem;
    font-weight: 700;
    color: var(--primary-color);
    text-shadow: 0 0 8px rgba(0, 255, 222, 0.8);
    margin-bottom: 2rem;
}

.schedule-board {
    width: 100%;
    max-width: 1200px;
    background-color: var(--menu-bg-color);
    border-radius: 10px;
    padding: 2rem;
    border: 2px solid var(--border-color);
    box-shadow: 0 0 15px rgba(0, 255, 222, 0.5);
}

h2 {
    text-align: center;
    font-size: 3rem;
    color: var(--primary-color);
    margin-bottom: 2rem;
    text-transform: uppercase;
}

.timeline {
    list-style: none;
    padding: 0;
}

.timeline-item {
    display: flex;
    align-items: center;
    padding: 1.5rem;
    border-radius: 8px;
    margin-bottom: 1rem;
    transition: all 0.3s ease;
    border-left: 5px solid transparent;
}

.timeline-item.active {
    background-color: var(--active-bg-color);
    border-left: 5px solid var(--border-color);
    transform: scale(1.02);
}

.timeline-time {
    font-size: 2.5rem;
    font-weight: 700;
    width: 200px;
}

.timeline-event {
    font-size: 2.5rem;
    flex: 1;
}

.countdown-container {
    text-align: center;
    margin-top: 2rem;
}

.countdown-label {
    font-size: 1.8rem;
    color: var(--primary-color);
    margin-bottom: 1rem;
}

body[data-theme='light'] {
    --bg-color: #ffffff;
    --text-color: #000000;
    --primary-color: #ff8c00;
    --secondary-color: #ffffff;
    --accent-color: #e67e22;
    --border-color: #ff8c00;
    --active-bg-color: rgba(255, 140, 0, 0.15);
    --menu-bg-color: rgba(248, 249, 250, 0.95);

    background: var(--bg-color);
    color: var(--text-color);
}

@keyframes animOrangeShine {
    from {
        transform: translateY(0) rotate(45deg);
        opacity: 0;
    }
    to {
        transform: translateY(-100vh) rotate(45deg);
        opacity: 1;
    }
}

.orange-shine-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    overflow: hidden;
}

.orange-shine {
    position: absolute;
    top: 100%;
    left: 50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(ellipse at center, rgba(255, 140, 0, 0.3) 0%, rgba(255, 140, 0, 0) 70%);
    animation: animOrangeShine 10s linear infinite;
}

.countdown-timer {
    font-size: 4rem;
    font-weight: 700;
    color: var(--accent-color);
    text-shadow: 0 0 10px var(--accent-color);
}

@media (max-width: 768px) {
    .main-container.layout-split {
        flex-direction: column;
        padding: 1rem;
    }

    .main-container .clock-wrapper {
        flex: 0 0 auto; /* Do not grow or shrink */
        padding-right: 0;
        padding-bottom: 1rem;
        position: -webkit-sticky; /* For Safari */
        position: sticky;
        top: 0;
        background: transparent;
        z-index: 1;
    }

    .main-container.layout-split .schedule-board {
        flex: 1 1 auto; /* Grow to fill remaining space */
        border-left: none;
        border-top: 2px solid #00ffde;
        padding: 1rem;
        height: auto; /* Remove fixed height */
        max-height: calc(100vh - 200px); /* Adjust as needed */
    }

    .clock {
        font-size: 4rem;
        margin-bottom: 0.5rem;
    }

    .main-title {
        font-size: 3rem;
    }

    .timeline-item {
        padding: 0.8rem;
        margin-bottom: 0.5rem;
    }

    .timeline-time,
    .timeline-event {
        font-size: 1.2rem;
    }

    .timeline-time {
        width: 120px;
    }

    .countdown-container {
        margin-top: 0.5rem;
    }

    .countdown-label {
        font-size: 1.2rem;
    }

    .countdown-timer {
        font-size: 2.5rem;
    }

    .top-nav {
        top: 1rem;
        left: 1rem;
    }

    .bottom-logo-container {
        margin: 1rem;
    }

    .logo-image {
        width: 6rem;
    }
}


